import React from 'react';
import styled from 'styled-components';
import PortfolioTile from '../components/PortfolioTile/PortfolioTile';
import LineDiv from '../components/LineDiv/LineDiv';
import P from '../components/paragraph/P';

const StyledPortfolioPage = styled.div`
  width: 100%;
  margin: 0;
  padding-bottom: 15px;
  display: flex;
  align-items: center;
  flex-direction: column;
  position: relative;
`;

const StyledWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 15px;
  padding-top: 0;
  padding-left: 20px;
  padding-right: 20px;

  @media ${({ theme }) => theme.device.mobileM} {
    padding-left: 50px;
    padding-right: 50px;
  }

  @media ${({ theme }) => theme.device.tablet} {
    padding-left: 70px;
    padding-right: 70px;
  }

  @media ${({ theme }) => theme.device.laptop} {
    padding-left: 100px;
    padding-right: 100px;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    padding-left: 200px;
    padding-right: 200px;
  }

  @media ${({ theme }) => theme.device.desktop} {
    padding-left: 300px;
    padding-right: 300px;
  }
`;

const StyledH2 = styled.h2`
  padding: 0;
  margin: 10px 0 0 0;
  font-size: 5rem;
  z-index: 2;
  text-align: center;

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 6rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 7rem;
  }

  @media ${({ theme }) => theme.device.laptopL} {
    font-size: 7.5rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 8.5rem;
  }
`;

const StyledPortfolioTileH3 = styled.h3`
  font-size: 2rem;
  padding: 0;
  margin: 0;

  @media ${({ theme }) => theme.device.tablet} {
    font-size: 2.5rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    font-size: 3rem;
  }

  @media ${({ theme }) => theme.device.desktop} {
    font-size: 3.3rem;
  }
`;

const StyledPortfolioTileA = styled.a`
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: ${({ theme }) => theme.black};
  background-color: ${({ theme }) => theme.gray};
  font-size: 2rem;
  text-align: center;
  line-height: 50%;
  border: 2px solid ${({ theme }) => theme.black};
  padding: 0;
  margin: 0;
  transition: background-color 0.5s ease;
  cursor: default;

  @media ${({ theme }) => theme.device.tablet} {
    width: 60px;
    height: 60px;
    font-size: 2.5rem;
  }

  @media ${({ theme }) => theme.device.laptop} {
    width: 70px;
    height: 70px;
    font-size: 3rem;
    cursor: pointer;

    &:hover {
      background-color: ${({ theme }) => theme.lightgray};
    }
  }

  @media ${({ theme }) => theme.device.desktop} {
    width: 75px;
    height: 75px;
    font-size: 3.3rem;
  }
  /* margin-bottom: 10px; */
`;

